'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Transaction, fetchDataByType } from '@/utils/api';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { TransactionTable } from '@/components/TransactionTable';
import { ChartByMonth } from '@/components/ChartByMonth';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import TrendingUpIcon from '@mui/icons-material/TrendingUp';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorIcon from '@mui/icons-material/Error';
import PendingIcon from '@mui/icons-material/Pending';
import Link from 'next/link';
import { useSearchParams } from 'next/navigation';

const TYPE_CONFIG = {
  incoming: {
    title: 'Incoming Transactions',
    description: 'Money received from various sources',
    color: 'bg-blue-500',
    icon: '📥',
  },
  p2p: {
    title: 'P2P Transfers',
    description: 'Person-to-person money transfers',
    color: 'bg-purple-500',
    icon: '🔄',
  },
  withdrawal: {
    title: 'Withdrawals',
    description: 'Cash withdrawals and money sent out',
    color: 'bg-orange-500',
    icon: '💸',
  },
  bundle: {
    title: 'Bundle Purchases',
    description: 'Data bundle and package purchases',
    color: 'bg-indigo-500',
    icon: '📱',
  },
  airtime: {
    title: 'Airtime Purchases',
    description: 'Mobile airtime top-ups and purchases',
    color: 'bg-cyan-500',
    icon: '📞',
  },
  bank: {
    title: 'Bank Deposits',
    description: 'Money transfers to and from banks',
    color: 'bg-emerald-500',
    icon: '🏦',
  },
};

interface Props {
  type: string;
  searchParams?: { [key: string]: string | string[] | undefined };
}

export default function TransactionTypeClientPage({ type }: Props) {
  const searchParams = useSearchParams();
  const router = useRouter();
  const channel = searchParams?.get('channel') || '';

  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [loading, setLoading] = useState(true);
  const [lastUpdated, setLastUpdated] = useState<Date>(new Date());
  const [error, setError] = useState<string | null>(null);

  const config = TYPE_CONFIG[type as keyof typeof TYPE_CONFIG];

  useEffect(() => {
    if (!config) {
      router.push('/');
      return;
    }
    loadTransactionData();
  }, [type, channel]);

  const loadTransactionData = async () => {
    setLoading(true);
    setError(null);
    try {
      let fetched = await fetchDataByType(type, 200);
      if (channel) {
        fetched = fetched.filter((t: Transaction) => t.channel === channel);
      }
      setTransactions(fetched);
      setLastUpdated(new Date());
    } catch (err) {
      console.error('Error fetching data:', err);
      setError('Failed to load transaction data. Please check your backend.');
    } finally {
      setLoading(true);
    }
  };

  if (!config) return null;

  const totalAmount = transactions.reduce((sum, t) => sum + t.amount, 0);
  const totalCount = transactions.length;
  const avgAmount = totalCount ? totalAmount / totalCount : 0;

  const channelCounts = transactions.reduce((acc, t) => {
    acc[t.channel] = (acc[t.channel] || 0) + 1;
    return acc;
  }, {} as Record<string, number>);

  const formatCurrency = (amount: number) =>
    new Intl.NumberFormat('en-RW', {
      style: 'currency',
      currency: 'RWF',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format(amount);

  return (
    <div className="min-h-screen bg-gradient-to-br from-yellow-50 via-amber-50 to-white">
      <div className="bg-white shadow-lg border-b border-yellow-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center">
              <Link href="/">
                <Button variant="outline" size="sm" className="mr-4 border-yellow-300 hover:bg-yellow-50">
                  <ArrowBackIcon className="w-4 h-4 mr-2" />
                  Back to Dashboard
                </Button>
              </Link>
              <div className={`w-12 h-12 ${config.color} rounded-full flex items-center justify-center mr-4 text-white text-xl shadow-lg`}>
                {config.icon}
              </div>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">{config.title}</h1>
                <p className="text-sm text-gray-600">{config.description}</p>
              </div>
            </div>
            <div className="text-right">
              <p className="text-sm text-gray-600">Last updated</p>
              <p className="text-sm font-medium">{lastUpdated.toLocaleTimeString()}</p>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {error && (
          <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
            <div className="flex items-center">
              <ErrorIcon className="w-5 h-5 text-red-500 mr-2" />
              <p className="text-red-700">{error}</p>
            </div>
          </div>
        )}

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card className="shadow-lg border-yellow-200">
            <CardHeader className="flex justify-between pb-2">
              <CardTitle className="text-sm text-gray-600">Total Amount</CardTitle>
              <AccountBalanceWalletIcon className="w-4 h-4 text-yellow-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-gray-900">
                {loading ? <div className="h-8 bg-gray-200 rounded animate-pulse"></div> : formatCurrency(totalAmount)}
              </div>
              <p className="text-xs text-gray-600 mt-1">{type} transactions</p>
            </CardContent>
          </Card>

          <Card className="shadow-lg border-yellow-200">
            <CardHeader className="flex justify-between pb-2">
              <CardTitle className="text-sm text-gray-600">Total Count</CardTitle>
              <TrendingUpIcon className="w-4 h-4 text-yellow-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-gray-900">
                {loading ? <div className="h-8 bg-gray-200 rounded animate-pulse"></div> : totalCount.toLocaleString()}
              </div>
              <p className="text-xs text-gray-600 mt-1">Total transactions</p>
            </CardContent>
          </Card>

          <Card className="shadow-lg border-green-200">
            <CardHeader className="flex justify-between pb-2">
              <CardTitle className="text-sm text-gray-600">Average Amount</CardTitle>
              <CheckCircleIcon className="w-4 h-4 text-green-500" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">
                {loading ? <div className="h-8 bg-gray-200 rounded animate-pulse"></div> : formatCurrency(avgAmount)}
              </div>
              <p className="text-xs text-gray-600 mt-1">Per transaction</p>
            </CardContent>
          </Card>

          <Card className="shadow-lg border-blue-200">
            <CardHeader className="flex justify-between pb-2">
              <CardTitle className="text-sm text-gray-600">Channel Breakdown</CardTitle>
              <div className="flex gap-1">
                <CheckCircleIcon className="w-3 h-3 text-green-400" />
                <PendingIcon className="w-3 h-3 text-yellow-400" />
                <ErrorIcon className="w-3 h-3 text-red-400" />
              </div>
            </CardHeader>
            <CardContent>
              <div className="flex gap-2 flex-wrap">
                {Object.entries(channelCounts).map(([ch, count]) => (
                  <Badge
                    key={ch}
                    variant="secondary"
                    className="bg-blue-100 text-blue-800 cursor-pointer hover:underline"
                    onClick={() => router.push(`/${type}?channel=${ch}`)}
                  >
                    {count} {ch}
                  </Badge>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        <div className="mb-8">
          <ChartByMonth transactions={transactions} loading={loading} />
        </div>

        <TransactionTable
          transactions={transactions}
          loading={loading}
          title={`All ${config.title}`}
        />
      </div>
    </div>
  );
}
